












































import Vue from 'vue';
import { Editor, EditorContent, EditorMenuBar } from 'tiptap';
import {
  Bold,
  Italic,
  Underline,
  Strike,
  Image,
  ListItem,
  BulletList,
  OrderedList,
} from 'tiptap-extensions';
import { Command } from 'tiptap-commands';
import { extend, ValidationProvider } from 'vee-validate';
import { required } from 'vee-validate/dist/rules';
import Message from '@/components/common/Message.vue';
import { Post, PostForm } from '@/types/post';

extend('required', {
  ...required,
  message: ' .',
});

export default Vue.extend({
  components: {
    EditorContent, EditorMenuBar, ValidationProvider, Message,
  },
  props: ['post'],
  data() {
    return {
      title: '',
      options: ['', '', '', ''],
      sort: '',
      content: '',
      tags: '',
      editor: null,
    };
  },
  methods: {
    showImagePrompt(command: Command) {
      const src = prompt(' URL  ');
      if (src !== null) {
        command({ src });
      }
    },
    onClick() {
      const data: PostForm = {
        title: this.title,
        content: this.content,
        sort: this.sort,
        tags: this.tags,
        UserId: this.$store.state.user.user.id,
      };
      if (this.$route.params.post) {
        const payload = {
          id: this.$route.params.post.id,
          data: data,
        };
        this.$store.dispatch('post/update', payload)
          .then(() => {
            this.$router.push({ name: 'home' });
          });
      } else {
        this.$store.dispatch('post/write', data)
          .then(() => {
            this.$router.push({ name: 'home' });
          });
      }
    },
  },
  beforeDestroy() {
    this.editor.destroy();
  },
  mounted() {
    let content = '';
    const post = this.$route.params.post;

    if (this.$route.params.post) {
      // @ts-ignore
      this.title = post.title;
      // @ts-ignore
      this.sort = post.sort;
      // @ts-ignore
      content = post.content;
      // @ts-ignore
      this.tags = post.tags;
    }
    this.editor = new Editor({
      autoFocus: true,
      extensions: [
        new Bold(),
        new Italic(),
        new Underline(),
        new Strike(),
        new Image(),
        new ListItem(),
        new BulletList(),
        new OrderedList(),
      ],
      onUpdate: ({ getHTML }) => {
        this.content = getHTML();
      },
      content: content,
    });
  },
});
