
















import Vue from 'vue';
import { Post } from '@/types/Post';

export default Vue.extend({
  name: 'PostCard',
  props: ['isWriter'],
  data() {
    return {
      loading: false,
      content: '<p>Hello World!</p>',
      tags: null,
    };
  },
  computed: {
    post() {
      return this.$store.state.post.post;
    },
    isTags() {
      const tags: string = this.$store.state.post.post.tags;
      const isTags = tags !== '';
      if (isTags) {
        // @ts-ignore
        this.tags = tags.split(' ');
      }
      return isTags;
    },
  },
  methods: {
    createdAt(createdAt: string) {
      const date = new Date(createdAt);
      return `${date.getFullYear()}/${date.getMonth()}/${date.getDay()}`;
    },
    onRemove() {
      this.$store.dispatch('post/remove', this.post.id);
    },
    onUpdate() {
      this.$router.push({ name: 'write', params: { post: this.$props.post } });
    },
  },
  created() {
    this.loading = true;
    this.$store.dispatch('post/read', this.$route.params.postId)
      .then(() => {
        this.loading = false;
      });
  },
});
